.App
  position: relative
  padding: 20px

  &::after
    content: ""
    position: fixed
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    width: 100%
    height: 100%
    background: url('./assets/img/silver.jpg') no-repeat
    background-size: cover
    background-position: 50% 50%
    z-index: -1

  &__wrapper
    border-radius: 24px
    background: rgba(255, 255, 255, 0.38)
    backdrop-filter: blur(14px)
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25)
    padding: 60px 60px
    display: flex
    flex-direction: column
    align-items: center
    max-width: clamp(calc(100% - 40px), 1200px, 1200px)
    position: relative
    overflow: hidden

  &__header
    margin-bottom: 60px
    font-size: 2rem
    line-height: 1

  &__container
    display: flex
    flex-direction: column
    align-items: center
    gap: 60px