.button
  padding: 10px 19px
  border-radius: 18px
  border: none
  background: rgba(255, 255, 255, 0.6)
  box-shadow: -2px -2px 6px rgba(255, 255, 255, 0.5), 2px 2px 6px rgba(0, 0, 0, 0.25)
  cursor: pointer
  transition: all ease 0.24s

  &:active
    transform: scale(0.98)
    box-shadow: -1px -1px 4px rgba(255, 255, 255, 0.5), 1px 1px 4px rgba(0, 0, 0, 0.25)