.form
  display: flex
  justify-content: center
  position: relative
  gap: 30px
  align-items: center
  flex-wrap: wrap
  
  &.active::after
    content: ""
    position: absolute
    bottom: -20px
    height: 1px
    width: 40%
    background: black

  &__item
    display: flex
    gap: 14px     

  &__result
    width: 100%
    text-align: center
    line-height: 1.4

  &__answer
    font-family: "Roboto-LightItalic"
    font-size: 0.9rem